@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;400&family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=East+Sea+Dokdo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&family=East+Sea+Dokdo&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Josefin Sans", "Arial Narrow", Arial, sans-serif;
  /* background: #ecf0f1; */
}


#header {
  width: 100%;
 height: 80vh;
 margin-top:70px;
}
#header h1{
  margin:0 0 10px 0;
  font-size:48px;
  font-weight: 700;
  color:black;
}

#header .brand-name{
  color:#3498db;
}

.main_note {
  width: 500px;
  padding: 15px 10px 5px 10px;
  margin: 20px auto;
}

.my_notes h1 {
  color: #34495e;
}
.my_notes p {
  color: grey;
}

form {
  min-height: 50px;
  color: #ecf0f1;
  padding: 15px 10px 5px 10px;
  box-shadow: 3px 5px 15px -5px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  position: relative;
}

.menu_style{
  width: 100%;
  line-height: 60px;
  background-color: #34495e;
  display: flex;
  justify-content: space-around;
}

.searchbar{
  width: 50%;
  height: auto;
  margin:0 auto;
  text-align: center;
}

.searchbar input{
  margin: 30px 0;
  padding: 5px 10px;
  border-radius: 5px;
  font-family: source-code-pro,Menlo,monaco;
}

.navbar{
  background-color: #fff!important;
}
.navbar-brand{
  font-size: 1.9rem !important;
  color: #34495e;
}

.menu_active{
  font-weight: bold;
  border-bottom: 1px solid  #565387;

}
.navbar a:hover{
  color:3498db !important
}

#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding:10px 30px;
  border-radius:50px;
  transition: 0.5s;
  color: #3498db;
  border:2px solid #3498db;
  text-decoration:none;

}

#header .btn-get-started:hover{
  background: #3498db;
  color:#fff
}

.header-img{
  text-align: right;
}

#header .animated{
  animation:up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0%{
    transform:translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}


